.button {
  display: inline-flex;
  // display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  color: currentColor;
  // margin: 10px 0;
  // padding: 10px 20px;

  // color: $color-default-white;

  // background-color: $color-neon-blue;
  background-color: transparent;
  border: none;
  cursor: pointer;
  // background-color: transparent;

  // transition: opacity $trans-default;

  // &--red {
  //   background-color: $color-torch-red;
  // }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.8;
    }
  }
}

.button--fill {
  // display: flex;
  width: 100%;
  min-height: 68px;
  color: $inversion-text;
  background-color: $background-accented;

  &:active {
    color: $text;
  }
}

.button--clean {
  width: 100%;
  min-height: 68px;
  border: 1px solid $accented-line;
}
